.o-mp-map {
  border: 1px solid var(--gray-40);
  margin-top: 1.875em;
  padding: 1.875em 1.875em 0.9375em;
}
.o-mp-map h3 span {
  font-weight: 600;
}
.o-mp-map .highcharts-map-series .highcharts-point-select,
.o-mp-map .highcharts-map-series .highcharts-point:hover {
  stroke: var(--black) !important;
  stroke-width: 3px !important;
  transition: fill 500ms, fill-opacity 500ms, stroke-width 0 !important;
}
.o-mp-map .highcharts-map-series .highcharts-point {
  stroke: var(--black);
}
.o-mp-map .highcharts-map-series .highcharts-point.highcharts-null-point {
  stroke-width: 0 !important;
}
.o-mp-map .cfpb-chart-geo-state-outline-states .highcharts-point, .o-mp-map .cfpb-chart-geo-state-outline-metros .highcharts-point, .o-mp-map .cfpb-chart-geo-state-outline-counties .highcharts-point, .o-mp-map .cfpb-chart-geo-map-separators .highcharts-point {
  stroke: var(--black);
}
.o-mp-map .cfpb-chart-geo-data-outline-counties .highcharts-point, .o-mp-map .cfpb-chart-geo-data-outline-metros .highcharts-point {
  stroke: #838588;
}
.o-mp-map .highcharts-tooltip-box {
  fill: var(--white);
  fill-opacity: 1;
  stroke: var(--black);
}

.m-mp-map-tooltip {
  font-family: "Avenir Next", Arial, sans-serif;
}
.m-mp-map-tooltip dt {
  border-bottom: 1px solid var(--black);
  font-size: 12px;
  margin-bottom: 5px;
  padding-bottom: 5px;
  width: 100%;
  text-transform: none;
}
.m-mp-map-tooltip dd {
  display: block;
  font-size: 12px;
  padding: 2px 0;
}

.m-color-ramp {
  margin-top: 0.9375em;
  width: 100%;
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-0 {
  fill: var(--gray-5);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-1 {
  fill: var(--pacific-20);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-2 {
  fill: var(--pacific-40);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-3 {
  fill: var(--pacific-60);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-4 {
  fill: var(--pacific-80);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-5 {
  fill: var(--pacific);
}
.m-color-ramp[data-chart-color=blue] .a-ramp-value-6 {
  fill: var(--pacific-dark);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-0 {
  fill: var(--gray-5);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-1 {
  fill: var(--navy-20);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-2 {
  fill: var(--navy-40);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-3 {
  fill: var(--navy-60);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-4 {
  fill: var(--navy-80);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-5 {
  fill: var(--navy);
}
.m-color-ramp[data-chart-color=navy] .a-ramp-value-6 {
  fill: var(--navy-dark);
}
.m-color-ramp rect {
  font-size: 0.75em;
  height: 15px;
  stroke: #838588;
  stroke-miterlimit: 10;
  stroke-width: 0.5;
  width: 91.5px;
}
.m-color-ramp text {
  font-size: 0.75em;
  text-anchor: start;
}
@media only all and (max-width: 56.25em) {
  .m-color-ramp text {
    font-size: 1em;
  }
  .m-color-ramp tspan {
    font-size: 1em;
  }
}

#mp-line-chart-container,
#mp-map-container {
  overflow: inherit;
}
#mp-line-chart-container fieldset,
#mp-map-container fieldset {
  border: 0;
  margin-left: 0;
  padding: 0;
}
#mp-line-chart-container .content-l__col,
#mp-map-container .content-l__col {
  border-left-width: 0;
}
#mp-line-chart-container .m-field-helper-text,
#mp-map-container .m-field-helper-text {
  color: var(--gray-80);
  font-size: 0.75em;
}

#mp-line-chart-controls,
#mp-map-controls {
  margin-left: 0;
}

#mp-line-chart-geo,
#mp-map-geo {
  margin-left: 2px;
}

#mp-map-month-container label,
#mp-map-year-container label {
  font-size: 0.875em;
}